import { wait } from '@/utils'
import { DateTime } from 'luxon'

export interface Unit {
  id?: number
  name: string
}

export interface Item {
  id?: number
  code: string
  name: string
  unitID?: number
  price: number
}

export interface POSReceipt {
  details: ReceiptDetail[]
  subtotalBeforeDiscount: number
  totalItemDiscount: number
  subtotal: number
  tradeDiscount: number
  grandTotal: number
}

export interface Receipt extends POSReceipt {
  id: number
  docNo: string
  date: DateTime
}

export interface ReceiptDetail {
  itemID: number
  itemCode: string
  itemName: string
  itemUnitName: string
  itemPrice: number
  quantity: number
  discountAmount: number
  discountPercentage: number
  amount: number
}

export function createDataStore() {
  let nextUnitID = 1
  const piece = { id: nextUnitID++, name: 'ชิ้น' }
  const bottle = { id: nextUnitID++, name: 'ขวด' }
  const units: Unit[] = [
    piece,
    bottle,
    { id: nextUnitID++, name: 'กล่อง' },
    { id: nextUnitID++, name: 'กระป๋อง' },
  ]
  let nextItemID = 1
  const cookies = { id: nextItemID++, code: 'I001', name: 'Cookies', unitID: piece.id, price: 100 }
  const drinkingWater = { id: nextItemID++, code: 'I002', name: 'Drinking Water', unitID: bottle.id, price: 10 }
  const cola = { id: nextItemID++, code: 'I003', name: 'Cola', unitID: bottle.id, price: 50 }
  const chocolate = { id: nextItemID++, code: 'I004', name: 'Chocolate', unitID: piece.id, price: 200 }
  const items: Item[] = [
    cookies,
    drinkingWater,
    cola,
    chocolate,
  ]
  let nextTransactionID = 1
  const transactions: Receipt[] = []
  const referencedItems = new Set<number>()

  const datastore = {
    unit: {
      async getAllUnits(): Promise<Unit[]> {
        await wait(1000)
        return units.map(x => ({ ...x }))
      },
      async editUnit(unit: Unit) {
        await wait(1000)
        const find = units.find(x => x.id === unit.id)
        if (find) {
          if (units.find(x => x.id !== unit.id && x.name === unit.name))
            throw new Error('duplicated name')
          find.name = unit.name
        }
        else {
          if (units.find(x => x.name === unit.name))
            throw new Error('duplicated name')
          units.push({ id: nextUnitID++, ...unit })
        }
      },
      async deleteUnit(id: number) {
        await wait(1000)
        const index = units.findIndex(x => x.id === id)
        if (index === -1)
          throw new Error('not found')
        if (items.find(x => x.unitID === id))
          throw new Error('unitID already in used')
        units.splice(index, 1)
      },
    },
    item: {
      async getAllItems(): Promise<Item[]> {
        await wait(1000)
        return items.map(x => ({ ...x }))
      },
      async editItem(item: Item) {
        await wait(1000)
        const find = items.find(x => x.id === item.id)
        if (find) {
          if (items.find(x => x.id !== item.id && x.code === item.code))
            throw new Error('duplicated code')
          find.code = item.code
          find.name = item.name
          find.unitID = item.unitID
          find.price = item.price
        }
        else {
          if (items.find(x => x.code === item.code))
            throw new Error('duplicated code')
          items.push({ id: nextItemID++, ...item })
        }
      },
      async deleteItem(id: number) {
        await wait(1000)
        const index = items.findIndex(x => x.id === id)
        if (index === -1)
          throw new Error('not found')
        if (referencedItems.has(id))
          throw new Error('itemID already in used')
        items.splice(index, 1)
      },
    },
    receipt: {
      async getReceipts(from?: DateTime, to?: DateTime): Promise<Receipt[]>{
        await wait(1000)
        
        return transactions
          .filter(x => (from ? x.date >= from : true) && (to ? x.date <= to : true))
          .map(x => ({
            ...x,
            details: x.details.map(y => ({
              ...y
            })),
          }))
      },
      async getReceipt(id: number): Promise<Receipt>{
        await wait(1000)
        
        return transactions.find(x => x.id === id)!
      },
      async addReceipt(transaction: POSReceipt) {
        await wait(1000)

        for (const item of transaction.details) {
          if (!referencedItems.has(item.itemID))
            referencedItems.add(item.itemID)
        }

        transactions.push({
          docNo: genDocNo(nextTransactionID),
          id: nextTransactionID++,
          date: DateTime.now().set({ hour: 0, minute: 0, second: 0, millisecond: 0 }),
          ...transaction,
          details: transaction.details.map(x => ({ ...x }))
        })
        
        function genDocNo(runningNo: number){
          return `T${(''+runningNo).padStart(4, '0')}`
        }
      }
    }
  }
  datastore.receipt.addReceipt({
    details: [
      {
        itemID: cookies.id,
        itemCode: cookies.code,
        itemName: cookies.name,
        itemUnitName: units.find(x => x.id === cookies.unitID)?.name!,
        itemPrice: cookies.price,
        quantity: 1,
        discountAmount: 0,
        discountPercentage: 0,
        amount: cookies.price,
      },
    ],
    subtotalBeforeDiscount: 100,
    totalItemDiscount: 0,
    subtotal: 100,
    tradeDiscount: 0,
    grandTotal: 100,
  })
  return datastore
}

export type Datastore = ReturnType<typeof createDataStore>
